body {
  /*  (Dark Mode Elements) */
  --darkBlue: hsl(209, 23%, 22%);
  /* (Dark Mode Background) */
  --veryDarkBlue: hsl(207, 26%, 17%);
  /* (Light Mode Text) */
  --textDarkBlue: hsl(200, 15%, 8%);
  /* (Light Mode Input) */
  --darkGray: hsl(0, 0%, 87%);
  /* (Light Mode Background) */
  --veryLightGray: hsl(0, 0%, 98%);
  /* (Dark Mode Text & Light Mode Elements) */
  --white: hsl(0, 0%, 100%);
  --nav: hsl(0, 0%, 98%);
  --text: hsl(0, 0%, 52%);
  --bg: hsl(0, 0%, 100%);
  --grayText: hsl(0, 0%, 52%);
  --back: lightgray;

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Nunito Sans;
}

body.dark {
  --textDarkBlue: hsl(0, 0%, 100%);
  --veryLightGray: hsl(207, 26%, 17%);
  --darkGray: hsl(207, 26%, 17%);
  --white: hsl(207, 26%, 17%);
  --nav: hsl(209, 23%, 22%);
  --text: hsl(0, 0%, 100%);
  --bg: hsl(209, 23%, 22%);
  --grayText: hsla(0, 0%, 98%, 0.651);
  --back: hsl(209, 23%, 22%);
}

@media (prefers-color-scheme: dark) {
  body {
    --textDarkBlue: hsl(0, 0%, 100%);
    --veryLightGray: hsl(207, 26%, 17%);
    --darkGray: hsl(207, 26%, 17%);
    --white: hsl(207, 26%, 17%);
    --nav: hsl(209, 23%, 22%);
    --text: hsl(0, 0%, 100%);
    --bg: hsl(209, 23%, 22%);
    --grayText: hsla(0, 0%, 98%, 0.651);
    --back: hsl(209, 23%, 22%);
  }
}
