.card {
  background-color: var(--bg);
  box-shadow: 1px 1px 4px 1px var(--darkGray);
  width: 75%;
  border-radius: 5px;
  font-size: 0.8rem;
  margin-bottom: 3rem;
  padding-bottom: 1rem;
}

.flag {
  height: 150px;
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.cardContent {
  padding: 0 15px;
}

p {
  line-height: 0.6rem;
}

.link {
  color: var(--textDarkBlue);
  text-decoration: none;
}

.text {
  color: var(--grayText);
  line-height: 1.2rem;
}

h3 {
  color: var(--textDarkBlue);
}

@media screen and (min-width: 429px) {
  .card {
    width: 255px;
    margin-bottom: 2.5rem;
  }

  .flag {
    width: 255px;
  }
}

@media screen and (min-width: 1281px) {
  .card {
    width: 255px;
    margin-bottom: 1rem;
  }
}
