.wrapper {
  width: 85%;
  margin: 3.5rem auto 0 auto;
  padding-top: 2rem;
}

.backBtn {
  color: var(--textDarkBlue);
  text-decoration: none;
  background-color: var(--bg);
  padding: 0.5rem 1.5rem;
  border-radius: 5px;
  outline: 1px solid var(--darkGray);
  box-shadow: 0 2px 3px 1px var(--darkGray);
}

.backBtn::before {
  content: "← ";
}

.detailContainer {
  padding-top: 4.5rem;
}

.flag {
  width: 100%;
}

.middle {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.bottom {
  margin-top: 2rem;
}

.box {
  color: var(--grayText);
  font-size: 0.8rem;
  margin-top: 0;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
  padding: 0.4rem 2rem;
  border: 1px solid var(--darkGray);
  background-color: var(--bg);
  border-radius: 5px;
  box-shadow: 0px 1px 2px 1px var(--darkGray);
}

.border {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.text {
  color: var(--grayText);
  line-height: 1rem;
}

p {
  font-size: 0.8rem;
  color: var(--textDarkBlue);
}

h1 {
  font-size: 1.5rem;
  color: var(--textDarkBlue);
}

@media screen and (min-width: 429px) {
  .middle {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}

@media screen and (min-width: 811px) {
  .wrapper {
    width: 80%;
    margin: 3.5rem auto 0 auto;
    padding-top: 2rem;
  }

  .detailContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }

  .flag {
    width: 450px;
    height: 300px;
  }

  .middle {
    flex-direction: row;
    gap: 10rem;
  }

  .bottom {
    max-width: 800px;
    display: flex;
    align-items: center;
  }

  h1 {
    font-size: 2rem;
  }

  p {
    font-size: 1rem;
  }
}
