.navBar {
  position: fixed;
  width: 100%;
  z-index: 1;
  background-color: var(--nav);
  box-shadow: 0 0px 1px 1px var(--darkGray);
  border-bottom: 1px solid var(--darkGray);
}

.navContainer {
  width: 90%;
  height: 90px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.title {
  font-weight: bold;
  color: var(--textDarkBlue);
}

.themeToggle {
  display: flex;
  gap: 0.2rem;
  cursor: pointer;
}

.dark {
  color: var(--textDarkBlue);
  width: 18px;
  height: 18px;
}

.text {
  font-size: 0.9rem;
  color: var(--textDarkBlue);
}

@media screen and (min-width: 429px) {
  .navContainer {
    width: 80%;
    height: 60px;
  }

  .title {
    font-size: 1.5rem;
  }

  .text {
    font-size: 1rem;
  }
}
