.app {
  margin-top: 5rem;
}
.mainSection {
  width: 90%;
  margin: 0 auto;
  padding-top: 2.5rem;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.search {
  width: 90%;
  margin: 0 auto;
  align-items: center;
}

.opt {
  cursor: pointer;
  width: 12rem;
  border-radius: 5px;
  background-color: var(--bg);
  border: none;
  padding: 1rem 1rem;
  box-shadow: 1px 1px 5px 2px var(--darkGray);
  color: var(--textDarkBlue);
  margin-top: 2.5rem;
  -webkit-appearance: inherit;
  -moz-appearance: inherit;
}
.opt:focus {
  outline: none;
}
.def::after {
  margin-left: -2rem;
  content: "▼";
  font-size: 0.9rem;
  color: var(--textDarkBlue);
}

.loadingContainer {
  display: flex;
  margin-top: 10rem;
  justify-content: center;
  align-items: center;
}

@media screen and (min-width: 429px) {
  .content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
  }

  .mainSection {
    width: 80%;
    margin: 0 auto;
    align-items: center;
  }

  .search {
    width: 80%;
    display: flex;
    justify-content: space-between;
  }

  .opt {
    margin-top: 0;
  }
}

@media screen and (min-width: 811px) {
  .content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

@media screen and (min-width: 1281px) {
  .content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

@media screen and (min-width: 1920px) {
  .content {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 3.5rem;
  }
}
