.searchBar {
  position: relative;
}

.search {
  position: absolute;
  left: 15px;
  top: 16px;
  height: 18px;
  width: 18px;
  color: var(--text);
}

input {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem;
  border: none;
  color: var(--textDarkBlue);
  border-radius: 5px;
  box-shadow: 1px 1px 5px 2px var(--darkGray);
  background-color: var(--bg);
}

input::placeholder {
  color: var(--text);
}

input:focus {
  outline: none;
}

@media screen and (min-width: 429px) {
  input {
    width: 25rem;
  }
}
